
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  height: 48px;
  line-height: 48px;
  width: 100%;
  z-index: 150;
  background: #f5f5f5;
  border-top: 1px solid rgba(7, 17, 27, 0.2);
}
.footer .common {
  flex: 1;
  text-align: center;
  color: #3c9;
}
.footer .iconfont {
  font-size: 24px;
}
.footer__mosk {
  position: fixed;
  top: 65%;
  left: 30%;
  bottom: 48px;
  text-align: center;
  height: 34px;
  color: #fff;
  background: rgba(0, 0, 0, 0.6);
  font-size: 14px;
  padding: 5px 10px;
  box-sizing: border-box;
  line-height: 22px;
  z-index: -1;
  border-radius: 5px;
}
.on {
  color: #3c9;
}



