/*
1像素边框
 */
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px:after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px:after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
/*
行高
 */
/*
字体
 */
.header {
  position: relative;
  z-index: 90;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 64px;
  background: url(../../assets/images/bd.png) no-repeat;
  box-sizing: border-box;
}
.header__logo {
  padding-left: 10px;
}
.header__img {
  width: 91px;
  height: 35px;
}
.header__nav-btn {
  display: block;
  width: 40px;
  height: 40px;
  background: url(../../assets/images/sprite.png) no-repeat;
  background-position: -40px 0;
  transform: scale(0.5, 0.5);
}
/*
浮层
 */
.mosk {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 151;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 75%;
  background: #366;
  transform: translate3d(0, 0, 0);
  box-shadow: -1px 0 1px 1px #366;
  opacity: 1;
}
.mosk__header {
  flex: 1;
}
.mosk__header-nav {
  width: 100%;
}
.mosk__header-close {
  display: flex;
  height: 44px;
  line-height: 44px;
  padding: 28px 16px;
  background: rgba(0, 0, 0, 0.1);
}
.mosk__header-close .from {
  flex: 1;
  position: relative;
  display: flex;
}
.mosk__header-close .from > input {
  border: none;
  background: #264a4a;
  border-radius: 25px;
  font-size: 12px;
  padding: 10px 10px 10px 40px;
  outline: none;
}
.mosk__header-close .from .search-btn {
  position: absolute;
  top: 50%;
  text-align: center;
  width: 30px;
  height: 30px;
  left: 10px;
  margin-top: -15px;
  outline: none;
}
.mosk__header-close .icon {
  display: inline-block;
  width: 40px;
  height: 40px;
  background: url(../../assets/images/sprite.png) no-repeat;
  background-position: -80px 0;
  transform: scale(0.5, 0.5);
}
.mosk__header-close .search-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  margin: -8px 0 0 -8px;
  background: url(../../assets/images/search.png) no-repeat;
}
.mosk__header-li {
  position: relative;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  height: 44px;
  line-height: 44px;
}
.mosk__header-li:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.mosk__header-li > a {
  display: block;
  font-size: 14px;
  color: #3c9;
  text-decoration: none;
}
.mosk__footer {
  text-align: center;
  padding-bottom: 20px;
}
.mosk__footer-pic {
  padding-bottom: 15px;
}
.mosk__footer-pic > img {
  width: 64px;
  height: 64px;
  border-radius: 50%;
}
.mosk__footer-login {
  font-size: 14px;
  color: rgba(51, 204, 153, 0.4);
  padding-top: 20px;
}
.mosk__footer .copyright-text {
  font-size: 12px;
  color: #3c9;
  padding-right: 10px;
  border-right: 1px solid rgba(51, 204, 153, 0.4);
  height: 12px;
  display: inline-block;
}
/*
动画
 */
.fadeLeft-enter,
.fadeLeft-leave-to {
  transform: translate3d(100%, 0, 0);
  opacity: 0;
}
.fadeLeft-enter-active,
.fadeLeft-leave-active {
  transition: all 0.5s;
}
.fade-x-enter,
.fade-x-leave-to {
  transform: translate3d(0, 100%, 0);
  opacity: 0;
}
.fade-x-enter-active,
.fade-x-leave-active {
  transition: all 0.5s;
}



