
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px:after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px:after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
/*动画*/
.move-enter,
.move-leave-to {
  transform: translate3d(100%, 0, 0);
  opacity: 0;
}
.move-enter-active,
.move-leave-active {
  transition: all 0.3s linear;
}
.city-info {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 49px;
  right: 0;
  z-index: 120;
  background: #f5f5f5;
  opacity: 1;
  overflow: hidden;
}
.city-info .close {
  position: absolute;
  top: 10px;
  left: 0;
  padding: 10px;
  font-size: 24px;
  color: rgba(255, 255, 255, 0.8);
  z-index: 111;
}
.city-info__img {
  position: relative;
}
.city-info__img > img {
  width: 100%;
  height: 100%;
}
.city-info .title {
  position: absolute;
  width: 90%;
  bottom: 30px;
  left: 10px;
  box-sizing: border-box;
  color: #fff;
  font-weight: normal;
  font-size: 30px;
}
.city-info__guide {
  position: relative;
  padding: 10px;
  background: #fff;
}
.city-info__guide:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.city-info__guide .intro {
  font-size: 14px;
  color: #333;
  line-height: 1.5;
}
.city-info-title {
  background: #fff;
  width: 100%;
  overflow: hidden;
  padding: 10px;
}
.city-info-title .hot-spot {
  width: 175px;
  display: inline-block;
  margin-right: 5px;
  padding-bottom: 10px;
}
.city-info-title .hot-spot .img {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 5px 5px 0 0;
}
.city-info-title .spot-name {
  background: #f5f5f5;
  text-align: center;
  padding: 10px;
  color: #333;
  font-size: 12px;
}
.city-info .h2title {
  font-size: 16px;
  color: #111;
  line-height: 30px;
  background: #fff;
  padding: 10px;
}
.city-info .h2title:before {
  display: inline-block;
  content: "";
  margin-right: 10px;
  height: 15px;
  width: 4px;
  background: #3c9;
}
.style {
  width: 100%;
  height: 10px;
}
.goods__list-li {
  position: relative;
  box-sizing: border-box;
  padding: 5px;
  width: 100%;
}
.goods__list-pics {
  position: relative;
  background: #333;
  border-radius: 4px;
  width: 100%;
}
.goods__list-pics .list-pics__img {
  width: 100%;
  height: 100%;
  border-radius: 4px;
  border: none;
}
.goods__list-detail {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 3;
  transform: translate(-50%, -50%);
  width: 80%;
  text-align: center;
}
.goods__list-title {
  font-size: 24px;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: normal;
}
.goods__list-ptag {
  font-size: 16px;
  color: #fff;
  margin-top: 8px;
  font-weight: normal;
}
.goods__list .goods__mosk {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
  z-index: 2;
}



