
.myfourite {
  position: absolute;
  top: 130px;
  bottom: 0;
  width: 100%;
  left: 0;
  right: 0;
  z-index: 10;
}
.goods__list {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  overflow: auto;
}
.goods__list-li {
  position: relative;
  box-sizing: border-box;
  padding: 5px;
  width: 100%;
}
.goods__list-pics {
  position: relative;
  background: #333;
  border-radius: 4px;
  width: 100%;
}
.goods__list-pics .list-pics__img {
  width: 100%;
  height: 100%;
  border-radius: 4px;
  border: none;
}
.goods__list-detail {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 3;
  transform: translate(-50%, -50%);
  width: 80%;
  text-align: center;
}
.goods__list-title {
  font-size: 24px;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: normal;
}
.goods__list-ptag {
  font-size: 16px;
  color: #fff;
  margin-top: 8px;
  font-weight: normal;
}
.goods__list .goods__mosk {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
  z-index: 2;
}
.list__title {
  ont-size: 14px;
  color: #d2d2d2;
  line-height: 30px;
  text-align: center;
  margin-top: 20px;
}



