
.slide-show {
  position: relative;
  width: 100%;
  box-sizing: border-box;
}
.slide-show__img {
  position: relative;
  width: 100%;
  height: 205px;
  overflow: hidden;
}
.slide-show__img > img {
  width: 100%;
  height: 100%;
}
.slide-show__ul {
  position: absolute;
  bottom: 10px;
  left: 0;
  width: 100%;
  text-align: center;
}
.slide-show__li {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 50%;
  margin-left: 10px;
}
.slide-show__li > a {
  display: block;
  height: 10px;
}
.slide-show .on {
  background: rgba(51, 187, 142, 0.7);
}
.slide-trans-enter-active {
  transition: all .6s;
}
.slide-trans-enter {
  transform: translateX(365px);
}
.slide-trans-old-leave {
  transform: translateX(365px);
}
.slide-trans-old-leave-active {
  transition: all .6s;
}



