
.person {
  position: absolute;
  top: 64px;
  bottom: 50px;
  width: 100%;
  overflow: hidden;
}
.person__header {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}
.person__header img {
  width: 64px;
  height: 64px;
  border-radius: 50%;
}
.person__header-user {
  flex: 1;
  margin-left: 20px;
}
.person__header-name {
  font-size: 16px;
  line-height: 30px;
  color: #111;
  padding: 0 0 5px 20px;
}
.person__header-info {
  display: flex;
  justify-content: space-between;
}
.person__header-text {
  text-align: center;
  flex: 1;
  border-right: 1px solid #ccf3e6;
}
.person__header .icon {
  display: block;
  margin-bottom: 5px;
  color: #3c9;
}
.person__header span {
  font-size: 12px;
  color: #3c9;
}
.person__tab {
  display: flex;
  height: 40px;
  line-height: 40px;
  background: #f5f5f5;
  border-bottom: 1px solid rgba(7, 17, 27, 0.2);
}
.person__tab-item {
  flex: 1;
  text-align: center;
  font-size: 16px;
  color: #333;
  text-decoration: none;
}
.person__tab .active {
  border-bottom: 2px solid #3c9;
  transition: all 0.5s;
}



