
.star {
  font-size: 0;
}
.star .star__item {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 6px;
}
.star .on {
  background: url(star48_on@2x.png) no-repeat;
  background-size: 16px 16px;
}
.star .helf {
  background: url(star48_half@2x.png) no-repeat;
  background-size: 16px 16px;
}
.star .off {
  background: url(star48_off@2x.png) no-repeat;
  background-size: 16px 16px;
}



