/*1像素边框*/
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px:after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px:after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.comment__menu .comment__list {
  position: relative;
  display: flex;
  padding: 15px 0;
}
.comment__menu .comment__list:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.comment__menu .comment__list-pic {
  padding-right: 20px;
}
.comment__menu .comment__list-pic > img {
  border-radius: 50%;
}
.comment__menu .comment__list-user {
  flex: 1;
  font-size: 14px;
  color: #666;
  line-height: 1.5;
}
.comment__menu .comment__list-user > p {
  padding-bottom: 10px;
}
.comment__menu .comment__list-username {
  color: #3c9;
}



