/*1像素边框*/
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px:after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px:after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
/*行高*/
/*字体*/
.commons .em {
  padding-right: 5px;
  font-weight: bold;
}
/*动画*/
.move-enter,
.move-leave-to {
  transform: translate3d(100%, 0, 0);
  opacity: 0;
}
.move-enter-active,
.move-leave-active {
  transition: all 0.3s linear;
}
.viewspot {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 120;
  background: #f5f5f5;
  opacity: 1;
}
.viewspot .close {
  position: absolute;
  top: 0;
  left: 0;
  padding: 20px;
  font-size: 24px;
  color: rgba(255, 255, 255, 0.8);
  z-index: 111;
}
.viewspot__intro {
  position: relative;
  bottom: 48px;
}
.viewspot__intro-img {
  position: relative;
  width: 100%;
  padding-top: 90%;
}
.viewspot__intro-img > img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.viewspot__intro .viewspot__mosk {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
  z-index: 2;
}
.viewspot__intro-title {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 3;
  transform: translate(-50%, -50%);
  width: 80%;
  text-align: center;
}
.viewspot__intro .title {
  font-size: 24px;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: lighter;
}
.viewspot__intro .ptag {
  font-size: 16px;
  color: #fff;
  margin-top: 8px;
  font-weight: lighter;
}
.viewspot__intro-text {
  position: relative;
  padding: 16px;
  font-size: 14px;
  color: #666;
  line-height: 1.5;
}
.viewspot__intro-text:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.viewspot__intro .first {
  padding-bottom: 15px;
}
.viewspot__intro .em {
  padding-right: 5px;
  font-weight: bold;
}
/*景点列表*/
.menu {
  position: relative;
  /*列表*/
}
.menu__title {
  position: relative;
  padding: 10px 0;
}
.menu__title:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7, 17, 27, 0.2);
}
.menu__title .common {
  font-size: 12px;
  color: #3c9;
}
.menu__title-date {
  padding-left: 15px;
}
.menu__title-date:before {
  display: inline-block;
  content: "";
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #3c9;
  margin-right: 20px;
  border: 2px solid #f5f5f5;
  box-sizing: border-box;
}
.menu__title-spot {
  padding: 10px 0 0 50px;
}
.menu__title-overview {
  font-size: 12px;
  color: #666;
  padding: 15px 15px 15px 50px;
  line-height: 1.5;
}
.menu .list {
  position: relative;
  padding: 0 15px 25px 15px;
  z-index: 5;
}
.menu .list:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7, 17, 27, 0.2);
}
.menu .list__header {
  padding: 15px 0 15px 0;
}
.menu .list__title {
  display: inline-block;
  font-size: 16px;
  color: #333;
  line-height: 1.5;
}
.menu .list .icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  background: #3c9;
  border-radius: 50%;
  text-align: center;
  line-height: 20px;
  color: #f5f5f5;
  box-sizing: border-box;
  border: 2px solid #f5f5f5;
  margin-left: -5px;
}
.menu .list__warrper {
  width: 100%;
  overflow: hidden;
}
.menu .list__menu {
  width: 100%;
  height: 275px;
  overflow: hidden;
}
@media only screen and (max-width: 320px) {
.menu .list__menu {
    height: 230px;
}
}
.menu .list__img {
  width: 100%;
}
.menu .list__img > img {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}
.menu .list__text {
  background: #fff;
  padding: 0 10px;
  font-size: 14px;
  color: #666;
  line-height: 1.5;
}
.menu .list__text-tag {
  font-size: 16px;
  font-weight: 700;
  padding: 10px 0;
}
.menu .list__text-common {
  padding-bottom: 15px;
}
.menu .list .em {
  padding-right: 5px;
  font-weight: bold;
}
.menu .line {
  position: absolute;
  top: 30px;
  left: 21px;
  z-index: 0;
  width: 4px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.06);
}
.menu__end {
  position: relative;
}
.menu__end-logo {
  display: inline-block;
  width: 194px;
  height: 68px;
  background: url(../../assets/images/end-bd.png) no-repeat left center;
  transform: scale(0.5, 0.5);
}
.star-warrper {
  padding: 5px 0;
}
.style {
  width: 100%;
  height: 10px;
  background: rgba(0, 0, 0, 0.04);
}
.comment {
  position: relative;
  padding: 15px 15px 0 15px;
  bottom: 48px;
}
.comment__title {
  font-size: 14px;
  height: 48px;
  line-height: 48px;
  color: #666;
}
.comment__textarea .textarea {
  display: block;
  width: 100%;
  height: 90px;
  padding: 8px;
  box-sizing: border-box;
  font-size: 14px;
  resize: none;
  color: #333;
  border-top: 0;
  border-right: 1px solid #e6e6e6;
  border-bottom: 0;
  border-left: 1px solid #e6e6e6;
  border-radius: 0;
  background-color: #fff;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none;
}
.comment__words {
  padding: 5px;
  font-size: 12px;
  line-height: 1;
  text-align: right;
  color: #999;
  border-right: 1px solid #e6e6e6;
  border-left: 1px solid #e6e6e6;
  background-color: white;
}
.comment__submit {
  display: block;
  height: 44px;
  overflow: hidden;
  font-size: 1.1429em;
  line-height: 44px;
  cursor: default;
  text-align: center;
  text-decoration: none;
  color: white;
  border: 1px solid #e6e6e6;
  background-color: #e6e6e6;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.comment .haswords {
  background: #3c9;
}
.right__nav {
  position: fixed;
  bottom: 65px;
  right: 0;
  width: 50px;
  height: 50px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 6px 0 0 6px;
}
.right__nav__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -10px 0 0 -15px;
  background: url(../../assets/images/down.png) no-repeat;
  background-size: 100% 100%;
  width: 30px;
  height: 20px;
  transform: rotate(-90deg);
}
.right__nav .down1 {
  animation: down 1s linear infinite;
}
.right__nav-warrper {
  position: relative;
  height: 100%;
}
@keyframes down {
0% {
    transform: translateX(0) rotate(-90deg);
    opacity: 1;
}
100% {
    transform: translateX(20px) rotate(-90deg);
    opacity: 0;
    width: 25px;
    margin-left: -5px;
}
}
.scroll__menu {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 70%;
  z-index: 151;
}
.scroll__menu-mosk {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 70%;
  background: rgba(0, 0, 0, 0.6);
}
.scroll__menu-ul {
  display: flex;
  height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.2);
  position: fixed;
  z-index: 100;
  width: 100%;
}
.scroll__menu-li {
  display: flex;
  align-items: center;
  flex: 1;
}
.scroll__menu-ptag {
  font-size: 14px;
  color: #fff;
  line-height: 50px;
  padding-left: 30px;
  font-weight: normal;
  letter-spacing: 1px;
}
.scroll__menu-ptag:before {
  display: inline-block;
  content: "";
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #3c8;
  margin-right: 15px;
  opacity: 0.8;
}
.scroll__menu .scroll__line {
  position: absolute;
  top: 80px;
  width: 2px;
  height: 80%;
  background: rgba(255, 255, 255, 0.4);
  left: 35px;
}
.scroll__menu .icon-close {
  position: absolute;
  z-index: 160;
  bottom: 20px;
  right: 20px;
  font-size: 24px;
  color: #fff;
  padding: 5px;
}
.scroll__menu .click-active {
  color: #3c9;
}
.scroll-enter,
.scroll-leave-to {
  transform: transition3d(70%, 0, 0);
  opacity: 0;
}
.scroll-enter-active,
.scroll-leave-active {
  transition: all 0.5s;
}



